<template>
  <div class="hello">
    <div v-html="blogPage" />
    <br>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blogPage: ""
    };
  },
  mounted() {
    window.scroll(0, 0);
  },
  updated() {
    window.scroll(0, 0);
  },
  created() {
    this.getMarkdown();
  },
  watch: {
    $route: function(val) {
      this.getMarkdown();
    }
  },
  methods: {
    getMarkdown: function() {
      try {
        this.blogPage = require("@/assets/markdown/blog" +
          this.$route.params.id +
          ".md");
      } catch (e) {
        this.blogPage =
          "这篇文章未上传，请联系：<a href='mailto:sharealex@163.com'>sharealex@163.com</a>";
      }
    }
  }
};
</script>


